@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom styles */
@layer components {
  .emoji-preview img {
    max-width: 256px;
    max-height: 256px;
    width: auto;
    height: auto;
    object-fit: contain;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.1));
  }
}

/* Override for emoji-mart styles */
em-emoji {
  display: inline-block;
  height: 1em;
  min-width: 1em;
}